'use strict';

var PROPUBLICA_API = 'TWpoquMhTZ7eWzWg0704e6Pw7KM2UFyj1SmGjYbx';
var PROPUBLICA_BASE_URL = 'https://api.propublica.org/campaign-finance/v1/2016/races/';
var propublicaApp = {};

propublicaApp.getCandidates = function (state) {
    return $.ajax({
        url: PROPUBLICA_BASE_URL + state + '.json',
        dataType: 'json',
        method: 'GET',
        beforeSend: function beforeSend(request) {
            request.setRequestHeader('X-API-Key', PROPUBLICA_API);
        }
    }).then(function (results) {
        var senateCandidates = results.results.filter(function (candidate) {
            return candidate.district === '/races/CA/senate.json';
        });

        console.log(senateCandidates);
    });
};

propublicaApp.getCandidates('CA');